<template>
    <div id="reactivo">
        <h2>{{ titulo }}</h2>
        <input type="text" v-model="nombre" placeholder="nombre"/>
        <input type="text" v-model="edad" placeholder="edad"/>
        <p>Hola {{ nombre }}</p>
        <p class="success" v-if="!edad"></p>
        <p class="success" v-else-if="edad>=18">Tienes más de 18 años</p>
        <p class="alarm" v-else>Eres menor de edad</p>        
        <pre>{{ $data }}</pre>
        <input type="button" @click="alerta('sigues viviendo')" @mouseover="console.log('Has hecho mouse over')" value="pulsa o muere"/>
        
    </div>
</template>

<script>
export default {
    name: 'Reactivo',
    methods: {
        alerta: function(text) {
            alert(text)
        }
    },
    data() {
        return {
            titulo: 'Reactividad',
            nombre: '',
            edad: 0
        }

        
    }
}
</script>

<style>
    #reactivo{
        color: darkblue;
        text-align: center !important;
    }
    .success {
        color:green;
    }
    .alarm {color:red}
</style>


