<template>
  <div id="contacto">
    <h2>Escribe tu nombre</h2>
    <input type="text" id="nombre" v-model="nombre" placeholder="nombre" />
    <input type="text" id="apellidos" v-model="apellidos" placeholder="apellidos"/>
    <input type="button" v-on:click="saludar()" value="añadir" />
  </div>
</template>
<script>
export default {
  name: "Contacto",
  methods: {
      saludar () {
          this.$router.push({name: 'saludo', params: {datos: this.nombre+" "+this.apellidos}})

            //console.log ();
      }
  },
  data() {
    return {
      nombre: '',
      apellidos: '',
    };
  }
};
</script>
<style scoped>
#contacto {
    text-align: center;
}

#contacto input {
    display: block;
    margin: 10px auto;
    
}
</style>