
<template>
    <div id="usuarios">
        <h2>Listado asíncrono por HTTP (usuarios req-res)</h2>        
        <article class="usuario" v-for="(usuario) in usuarios" :key="usuario.id">
            <img :src="usuario.avatar" :alt="usuario.id" />
            <h4>{{usuario.first_name}} {{usuario.last_name}} <br />(ID: {{usuario.id}})</h4>
            <p>Email:  {{usuario.email}} </p>
        </article>
    </div>   
</template>
<script>
import axios from 'axios';

export default {
  name: "Apirest",
  mounted(){
    this.getUsuarios();
  },
  data() {
    return {
      usuarios: []
    }
  },
  methods: {
      getUsuarios(){
        axios.get("https://reqres.in/api/users?page=2")
          .then (res => {
            this.usuarios=res.data.data;
          })
      }
} 
};
</script>

<style scoped>
#usuarios {
  width: 600px;
  margin: 0px auto;
  border: 0px;
  padding: 5px;
  display: flex;
  flex-flow: row wrap;
  justify-content:space-between;
}

.usuario {
  background-color: rgb(230, 230, 146);
}

.usuario {
  width: 250px;
  margin: 10px;
}

.usuario img{
  float: left;
  margin-right: 10px;
}

.usuario p {
  clear: left;
  padding-top: 10px;
}
</style>
