import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router';
import db from './firebase/firebase';
import colecciones from './firebase/firebase';

import Home from "./components/Home.vue";
import Reactivo from "./components/Reactivo.vue";
import Listado from "./components/Listado.vue";
import Peliculas from "./components/Peliculas.vue";
import Contacto from "./components/Contacto.vue";
import Saludo from "./components/Saludo.vue";
import Formulario from "./components/Formulario.vue";
//import Error from "./components/Error.vue";
import ApiRest from "./components/ApiRest.vue";
// import Nuevo from "./components/Nuevo.vue";
//import FBdata from "./components/FBdata.vue";



Vue.config.productionTip = false

Vue.use(VueRouter);

const routes = [
  {path: '/home', component: Home},
  {path: '/reactivo', component: Reactivo},
  {path: '/listado', component: Listado},
  {path: '/peliculas', component: Peliculas},
  {path: '/contacto', component: Contacto},
  {path: '/formulario', component: Formulario},
  {path: '/apirest', component: ApiRest},
 // {path: '/fb', component: FBdata},
  //{path: '/exper/:valor?', component: Nuevo},
  {path: '/saludo/:datos?', name:'saludo', component: Saludo},
  {path: '/',  redirect: '/home'},
  {path: '*',  redirect: '/home'},
  //{path: '*',  component: Error},
]

const router = new VueRouter({
  routes,
  colecciones,
  db,
  mode: 'history'
})
new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
