<template>
  <div id="listado">
    <h2>Listado de usuarios</h2>
    <input type="text" id="nombre" v-model="usNombre" placeholder="nombre" />
    <input type="text" id="apellidos" v-model="usApellidos" placeholder="apellidos"/>
    <input type="button" v-on:click="addUsuario()" value="añadir" />
    <ul>
        <li v-for="usuario in usuarios" :key="usuario.id" >
            {{usuario.id}}: {{usuario.nombre}} {{usuario.apellidos}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Listado",
  methods: {
      addUsuario () {
            this.usuarios.push({id: this.usuarios.length+1, nombre: this.usNombre, apellidos: this.usApellidos})
      }
  },
  data() {
    return {
      usuarios: [
        { id: 1, nombre: "Juan", apellidos: "Salvador" },
        { id: 2, nombre: "Eva", apellidos: "Casal" },
        { id: 3, nombre: "Pedro", apellidos: "Pérez" },
        { id: 4, nombre: "Ana", apellidos: "Gómez" }
      ],
      usNombre: '',
      usApellidos: '',
      cont:0
    };
  }
};
</script>
